"use client"

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { Input } from '@/components/ui/input'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

interface Job {
  id: string
  title: string
  client: string
  freelancer: string | null
  budget: number
  status: 'open' | 'in_progress' | 'completed' | 'cancelled' | 'disputed'
  category: string
  createdAt: string
  deadline: string
  applications: number
}

export default function AdminJobs() {
  const [jobs, setJobs] = useState<Job[]>([])
  const [filteredJobs, setFilteredJobs] = useState<Job[]>([])
  const [searchTerm, setSearchTerm] = useState('')
  const [statusFilter, setStatusFilter] = useState('all')
  const [categoryFilter, setCategoryFilter] = useState('all')
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Simulate fetching jobs
    setTimeout(() => {
      const mockJobs: Job[] = [
        {
          id: '1',
          title: 'React Developer Needed for E-commerce',
          client: 'Tech Startup Inc.',
          freelancer: 'Sarah Chen',
          budget: 2500,
          status: 'in_progress',
          category: 'Web Development',
          createdAt: '2024-01-15',
          deadline: '2024-02-15',
          applications: 12
        },
        {
          id: '2',
          title: 'UI/UX Design for Mobile App',
          client: 'Design Studio',
          freelancer: null,
          budget: 1500,
          status: 'open',
          category: 'Design',
          createdAt: '2024-01-14',
          deadline: '2024-02-14',
          applications: 8
        },
        {
          id: '3',
          title: 'WordPress Website Development',
          client: 'Small Business Owner',
          freelancer: 'Mike Johnson',
          budget: 800,
          status: 'completed',
          category: 'Web Development',
          createdAt: '2024-01-13',
          deadline: '2024-01-30',
          applications: 5
        },
        {
          id: '4',
          title: 'Logo Design for Startup',
          client: 'New Company',
          freelancer: 'Emma Wilson',
          budget: 500,
          status: 'disputed',
          category: 'Design',
          createdAt: '2024-01-12',
          deadline: '2024-01-25',
          applications: 15
        }
      ]
      setJobs(mockJobs)
      setFilteredJobs(mockJobs)
      setIsLoading(false)
    }, 1000)
  }, [])

  useEffect(() => {
    let filtered = jobs

    if (searchTerm) {
      filtered = filtered.filter(job => 
        job.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        job.client.toLowerCase().includes(searchTerm.toLowerCase()) ||
        (job.freelancer && job.freelancer.toLowerCase().includes(searchTerm.toLowerCase()))
      )
    }

    if (statusFilter !== 'all') {
      filtered = filtered.filter(job => job.status === statusFilter)
    }

    if (categoryFilter !== 'all') {
      filtered = filtered.filter(job => job.category === categoryFilter)
    }

    setFilteredJobs(filtered)
  }, [jobs, searchTerm, statusFilter, categoryFilter])

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'open':
        return 'bg-blue-100 text-blue-800'
      case 'in_progress':
        return 'bg-yellow-100 text-yellow-800'
      case 'completed':
        return 'bg-green-100 text-green-800'
      case 'cancelled':
        return 'bg-red-100 text-red-800'
      case 'disputed':
        return 'bg-orange-100 text-orange-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  const handleEditJob = (jobId: string) => {
    alert(`Edit job ${jobId}`)
  }

  const handleDeleteJob = (jobId: string) => {
    alert(`Delete job ${jobId}`)
  }

  const handleChangeStatus = (jobId: string, currentStatus: string) => {
    alert(`Change job ${jobId} status from ${currentStatus}`)
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <Link href="/" className="flex items-center space-x-2">
              <div className="w-8 h-8 bg-black rounded-lg flex items-center justify-center">
                <span className="text-white font-bold text-sm">QL</span>
              </div>
              <span className="font-poppins font-bold text-xl">Quick Lance Admin</span>
            </Link>
            
            <nav className="hidden md:flex items-center space-x-6">
              <Link href="/admin/dashboard" className="text-gray-600 hover:text-black">
                Dashboard
              </Link>
              <Link href="/admin/users" className="text-gray-600 hover:text-black">
                Users
              </Link>
              <Link href="/admin/jobs" className="text-black font-medium">
                Jobs
              </Link>
              <Link href="/admin/analytics" className="text-gray-600 hover:text-black">
                Analytics
              </Link>
            </nav>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Page Header */}
        <div className="mb-8">
          <h1 className="font-poppins font-bold text-3xl mb-2">Manage Jobs</h1>
          <p className="text-gray-600">View and manage all platform jobs</p>
        </div>

        {/* Filters */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle>Filters</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <label className="text-sm font-medium mb-2 block">Search</label>
                <Input
                  placeholder="Search by title, client, or freelancer..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </div>
              <div>
                <label className="text-sm font-medium mb-2 block">Status</label>
                <Select value={statusFilter} onValueChange={setStatusFilter}>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Status</SelectItem>
                    <SelectItem value="open">Open</SelectItem>
                    <SelectItem value="in_progress">In Progress</SelectItem>
                    <SelectItem value="completed">Completed</SelectItem>
                    <SelectItem value="cancelled">Cancelled</SelectItem>
                    <SelectItem value="disputed">Disputed</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div>
                <label className="text-sm font-medium mb-2 block">Category</label>
                <Select value={categoryFilter} onValueChange={setCategoryFilter}>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Categories</SelectItem>
                    <SelectItem value="Web Development">Web Development</SelectItem>
                    <SelectItem value="Design">Design</SelectItem>
                    <SelectItem value="Mobile Development">Mobile Development</SelectItem>
                    <SelectItem value="Writing">Writing</SelectItem>
                    <SelectItem value="Marketing">Marketing</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Jobs Table */}
        <Card>
          <CardHeader>
            <CardTitle>All Jobs ({filteredJobs.length})</CardTitle>
            <CardDescription>Manage job postings and disputes</CardDescription>
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <div className="space-y-3">
                {[1, 2, 3, 4].map((i) => (
                  <div key={i} className="h-16 bg-gray-200 rounded animate-pulse"></div>
                ))}
              </div>
            ) : (
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Title</TableHead>
                    <TableHead>Client</TableHead>
                    <TableHead>Freelancer</TableHead>
                    <TableHead>Budget</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Category</TableHead>
                    <TableHead>Applications</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredJobs.map((job) => (
                    <TableRow key={job.id}>
                      <TableCell className="font-medium">{job.title}</TableCell>
                      <TableCell>{job.client}</TableCell>
                      <TableCell>{job.freelancer || 'Not assigned'}</TableCell>
                      <TableCell>${job.budget}</TableCell>
                      <TableCell>
                        <Badge className={getStatusColor(job.status)}>
                          {job.status}
                        </Badge>
                      </TableCell>
                      <TableCell>{job.category}</TableCell>
                      <TableCell>{job.applications}</TableCell>
                      <TableCell>
                        <div className="flex space-x-2">
                          <Button 
                            variant="ghost" 
                            size="sm"
                            onClick={() => handleEditJob(job.id)}
                          >
                            Edit
                          </Button>
                          <Button 
                            variant="ghost" 
                            size="sm"
                            onClick={() => handleChangeStatus(job.id, job.status)}
                          >
                            Status
                          </Button>
                          <Button 
                            variant="ghost" 
                            size="sm"
                            className="text-red-600 hover:text-red-700"
                            onClick={() => handleDeleteJob(job.id)}
                          >
                            Delete
                          </Button>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
